  margin-top: 15px;
  width: 100%;
  border-right: 1px solid #ccc;

}

.schedul1Contents .PublicTable .date {
}

.schedul1Contents .PublicTable .event {
  text-align: left;
}

.schedul1Contents .PublicTable .eventComplete {
  text-align: left;
  text-decoration: line-through;
  color:#ccc;
}



</style>
