<template>
  <div class="exchangeTitle">
    &bull; 환율정보
    <div class="exchangerate" >
      <iframe class="exchangeIframe"  scrolling = "no" src="https://finance.naver.com/marketindex/exchangeList.naver"></iframe>
    </div>
  </div>
  <div class="stockArea" >
    &bull; 주식시세
    <div class="stockData" v-if="loaded==true">
      <table class="stockTable">
        <tr class="stockTRHeader">
        <td>
            종목
          </td>
          <td>
            {{ firstDate }}
          </td>
          <td>
            {{ secDate  }}

          </td>
        </tr>
        <template v-for="(item, i) in arrStockList" v-bind:key="i">
          <tr class="stockTR">
            <td class="stockTDTitle">
              {{ item.Name }}
            </td>
            <td class="stockTD">
              {{ item.Data[0].clpr.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}
            </td>
            <td class="stockTD">
              {{ item.Data[1].clpr.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}
            </td>
          </tr>
        </template>
      </table>
    </div>
  </div>


</template>

<script>
import axios from "axios";

export default {
  name: "ExchangeRadeCompo"
  ,data() {
    return {
      loaded:false,
      firstDate:"",
      secDate:"",
      arrStockList:[
        {url: "/naver",Name :"네이버",Data:"" },
        {url: "/kakao",Name :"카카오",Data:"" },
        {url: "/infovine",Name :"인포바인",Data:"" },
        {url: "/hecto",Name :"헥토이노베이션",Data:"" },

      ]
    }
  },
  created() {


    this.arrStockList.forEach((item,i) => {
      axios.get(item.url, {
        headers: {
          "Content-Type": "application/xml",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
        },
      })
          .then(response => {
            if(i==0){




              this.firstDate = response.data.response.body.items.item[0].basDt
              this.secDate = response.data.response.body.items.item[1].basDt


              this.firstDate = this.firstDate.substring(0,4) + "." + this.firstDate.substring(4,6) + "." + this.firstDate.substring(6,8);
              this.secDate = this.secDate.substring(0,4) + "." + this.secDate.substring(4,6) + "." + this.secDate.substring(6,8);


            }
            item.Data = response.data.response.body.items.item
            this.loaded = true;
          });
    })


    console.log(this.arrStockList)
    this.load = true



  }

}
</script>

<style scoped>
.exchangeTitle {
  float: left;

  font-size: 17px;

  margin-left: 10px;
  text-align: left;
  padding-top: 10px;
  /*border-bottom: 3px #888;*/
  /*border-bottom-style: double ;*/
  /*width: 100px;*/

}

.exchangeTitle .exchangerate {

}

.exchangeTitle .exchangerate .exchangeIframe{
  float: left;

  border: none;
  width: 260px;
  padding-top: 10px;
  padding-left:20px;

}

.stockArea  {
  float: left;
  text-align: left;
  margin-left: 100px;
  padding-top: 10px;
  padding-left:20px;
}

.stockArea .stockData  {

}

.stockArea .stockData .stockTable{
  text-align: center;
  border-bottom: 1px solid #ccc;
  width: 400px;

}

.stockArea .stockData .stockTable .stockTRHeader{
  background-color:#ccc;
  color:#fff;
  font-weight: bold;
}


.stockArea .stockData .stockTable .stockTR{
  padding-bottmo: 20px;
  margin-bottom: 10px;
}



.stockArea .stockData .stockTable .stockTR .stockTDTitle {
  text-align: left;
}



.stockArea .stockData .stockTable .stockTR .stockTD{
  text-align: center;
}



</style>
